"use client";
import { motion } from 'framer-motion';

const stats = [
    { value: "+100", label: "Clubes" },
    { value: "+500", label: "Canchas" },
    { value: "2.5M", label: "Impactos/mes" },
];

export default function MapSection() {
    return (
        <section id="network" className="py-24 bg-slate-900 relative border-t border-slate-100/10">
            <div className="container px-4 mx-auto text-center">
                <div className="mb-16">
                    <h2 className="text-3xl md:text-5xl font-bold text-white mb-6">
                        Cobertura <span className="text-padel-green">Nacional</span>
                    </h2>
                    <p className="text-slate-400 text-lg max-w-2xl mx-auto">
                        Estamos presentes en las principales ciudades del país, garantizando alcance donde más importa.
                    </p>
                </div>

                {/* Abstract Map Representation */}
                <div className="relative max-w-4xl mx-auto h-[400px] md:h-[500px] mb-20 bg-slate-800/30 rounded-3xl border border-white/5 flex items-center justify-center overflow-hidden">
                    {/* Simple Argentina Outline SVG or Dots */}
                    <div className="absolute inset-0 flex items-center justify-center opacity-20">
                        {/* This is a placeholder for a real map SVG */}
                        <svg viewBox="0 0 200 400" className="h-full w-full text-white fill-current">
                            <path d="M60,20 C70,10 90,10 100,20 C110,30 120,50 110,100 C100,150 120,200 110,300 C100,350 90,380 80,390 C70,380 60,350 60,300 C50,250 80,150 60,100 C50,50 50,30 60,20 Z" />
                        </svg>
                    </div>

                    {/* Glowing Dots representing cities */}
                    <div className="absolute top-1/4 left-1/2 w-4 h-4 bg-padel-green rounded-full shadow-[0_0_20px_rgba(223,255,0,0.6)] animate-pulse"></div>
                    <div className="absolute top-1/3 left-1/2 w-3 h-3 bg-padel-green rounded-full shadow-[0_0_15px_rgba(223,255,0,0.5)] translate-x-8 translate-y-4"></div>
                    <div className="absolute top-2/5 left-1/2 w-3 h-3 bg-padel-green rounded-full shadow-[0_0_15px_rgba(223,255,0,0.5)] -translate-x-12 translate-y-12"></div>

                    <p className="relative z-10 text-slate-500 font-mono text-sm">MAPA DE COBERTURA ACTIVA</p>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
                    {stats.map((stat, index) => (
                        <div key={index} className="p-6 rounded-2xl bg-white/5 border border-white/5">
                            <p className="text-4xl md:text-5xl font-bold text-white mb-2">{stat.value}</p>
                            <p className="text-slate-400 font-medium uppercase tracking-wider text-sm">{stat.label}</p>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
}
